	module.exports = {"data":{"markdownRemark":{"html":"<h2>Bootstrap Templates</h2>\n<h3>Overview</h3>\n<p>Bootstrap templates are prebuilt toolkits that make spinning up a new web page a breeze. If you have a general idea of your desired layout, or if you want to browse through a library of common layout templates for ideas, Bootstrap templates take much of the tediousness, and frustrations away form the initial build process. This assistance helps you to focus on the finer details of the project instead of wondering why CSS is not cooperating.</p>\n<h3>Getting Started</h3>\n<ul>\n<li>Bootstraps official webpage offers “Themes” instead of templates. Themes are just fully built out starter projects, while a template describes just a prebuilt HTML frame. Themes cost money and perhaps help out the novice developer, while many templates are open source and only provide the preliminary layout elements needed.</li>\n<li>Below is a list of layout templates, made specifically for for Bootstrap. Have Fun!</li>\n</ul>\n<h3>Template Links</h3>\n<ul>\n<li><a href=\"https://startbootstrap.com/\">StartBootstrap Templates</a></li>\n<li><a href=\"https://www.w3schools.com/bootstrap/bootstrap_templates.asp\">W3 Schools Bootstrap Templates</a></li>\n<li><a href=\"https://wrapbootstrap.com/\">Wrap Bootstrap</a></li>\n<li><a href=\"http://wowslider.com/posts/35-top-free-bootstrap-templates-2016-95.html\">Wow Slider</a></li>\n<li><a href=\"https://bootstrapmade.com/\">Bootstrap Made</a></li>\n</ul>","fields":{"slug":"/bootstrap/bootstrap-templates/"},"frontmatter":{"title":"Bootstrap Templates"}}},"pathContext":{"slug":"/bootstrap/bootstrap-templates/"}}

